@font-face {
  font-family: "ptsans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "ptsans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.main-header {
  background-color: #060608;
  font-family: "ptsans", sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 10px;
}

.page__body {
  background-color: #ffffff;
  margin: 0 auto;
  font-family: "ptsans", "Arial", sans-serif;
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
  color: #000000;
  font-size: 14px;
  line-height: 21px;
  height: 100%;
  box-sizing: border-box;
  font-weight: 400;
  line-height: 120%;
}

.main-nav__list {
  color: #ffffff;
  opacity: 0.6;
  padding: 0;
  font-weight: 400;
  line-height: 120%;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 17px;
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.main-nav__list-item-link {
  text-decoration: none;
  color: #ffffff;
  opacity: 0.7;
  font-size: 1.125em;
}

.main-nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.main-nav__list-item {
  margin-left: 47px;
}

.main-header__logo-image {
  margin: 15px 5px 6px 84px;
}

.main-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 72px;
}

.main-nav__submit {
  background-color: #060608;
  padding: 15px 30px;
  border: 2px solid #fff;
  color: #fff;
  font-size: 1.125em;
  border-radius: 5px;
  font-family: "ptsans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: 400;
  margin-left: 52px;
}

.description {
  background-color: #060608;
  font-family: "ptsans", sans-serif;
  color: #ffffff;
}

.description__title {
  margin-top: 0;
  font-weight: 700;
  line-height: 100%;
  font-size: 80px;
  padding-top: 65px;
  padding-left: 15px;
  margin-bottom: 0;
}

.description__text {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.description__slogan {
  grid-column: 1;
  font-weight: 400;
  line-height: 120%;
  font-size: 18px;
  margin: 20px 0 40px;
  padding-left: 15px;
}

.description-list {
  grid-column: 1;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}

.description-link {
  grid-column: 1;
}

.description-wrapper {
  display: flex;
  flex-wrap: wrap;
  grid-column: 1;
}

.description-link--white {
  width: 350px;
  height: 60px;
  color: #fff;
  background: #0050b2;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-family: "ptsans", sans-serif;
  font-size: 18px;
  font-weight: 550;
  border-radius: 5px;
  margin-left: 15px;
  margin-right: 15px;
}

.description-link--blue {
  color: #ffffff;
  font-size: 18px;
  font-weight: 550;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.description-slogan--second {
  margin-top: 40px;
}